import React from "react"
import Metro from "./components/Metro"


export default function App(){
const [bpm, setBpm]=React.useState(120)
const [isRunning, setIsRunning]=React.useState(false)
const tempStop=React.useRef(false)
const [entrance, setEntrance]=React.useState(true)
let noZero=React.useRef(false)
let lastTempo=React.useRef(120)
/* const [helper, setHelper]=React.useState(true) */
let mouseDown = React.useRef(false);
document.body.onmousedown = function() { 
    mouseDown.current = true;
    console.log("mousedown")
}
document.body.onmouseup = function() {
mouseDown.current = false;
}


function changeBPM(value){
 
 if(value==0 && isRunning==true){noZero.current=false}   
 if(value>0 && noZero.current==true){lastTempo.current=value;setBpm(value); reset()}
 else if(value>0 && noZero.current==false){lastTempo.current=value;setBpm(value); setIsRunning(true); reset(); noZero.current=true}
 else {setIsRunning(false); setBpm("")}
}
/*  if (value>0 && noZero.current==true) {console.log("case1");lastTempo.current=bpm;noZero.current=false;setIsRunning(1);setBpm(value);reset();}
 if (value>0 && noZero.current==false){console.log("case2");lastTempo.current=bpm;setBpm(value);reset();} */
/*  if (value===""){ console.log("case3");setIsRunning(0);setBpm("");noZero.current=true} 
 else {}
 */


function changeRunState(){
    if (noZero.current==false){noZero.current=true;setBpm(lastTempo.current)}
    setIsRunning(prevState=>!prevState)
}

function reset(){
    if(isRunning)
   {changeRunState()
    tempStop.current=true
  /*  setHelper(prevState=>!prevState) */}}

  function restart(){
if(tempStop.current){
    changeRunState()
    tempStop.current=false
}
  }

  function goIn(){
    setEntrance(prevState=>!prevState)
    document.querySelectorAll(".fa-clock").forEach(clock=>{clock.classList.remove("fa-spin");clock.classList.remove("fa-spin-pulse") })
  }

function fineSelect(){

document.body.dispatchEvent(new Event("mouseup"))
console.log(mouseDown)}


  


/* 
React.useEffect(changeRunState, []) */
/* React.useEffect(changeRunState, [helper]) */
React.useEffect(()=>console.log("isRunning: "+isRunning), [])


    return (
            <div className="container">
                <h1 className="mt-3"> P<span><i className="fa-regular fa-clock fa-spin" style={{color:"#fe79ae"}}></i></span>lycl<span><i className="fa-regular fa-clock fa-rotate-180 fa-spin-pulse"style={{color:"#fe79ae"}}></i></span>cker</h1>
                <h5 className="mb-1"><span>[ </span>polyrhythmic step-metronome<span> ]</span></h5>
                <div hidden={!entrance} className="mt-5 introTextContainer">
                    <p className="introText"> </p>
                    <b>Welcome to PolyClocker!</b> <br />Create the most complex polyrhythms on the spot through a dynamic and intuitive step interface. 
                    <ul className="mt-4 mb-5">
                        <li>choose a global tempo</li>
                        <li>set the desired beat subdivision for the two metronome modules</li>
                        <li>configure patterns by click</li>
                        <li>start both metronomes at once and in sync</li>
                        <li>change sound, gain and stereo panorama independently for better distinction</li>
                        <li>vary all parameters or clear everything on the fly!</li>
                    </ul>

                   
                </div>
                <div className="startButtonContainer">
                <button hidden={!entrance} onClick={goIn} className="btn btn-light fs-5 mt-5" style={{color: "grey"}}>Click to start!</button>
                </div>
                <main>
                <div hidden={entrance} className="metroSuperContainer short mt-5 p-4">
                <div className="startSectionWrapper">
                    
                <div className="controlBox verticalizer">
                    <label className="mb-3" hidden={entrance}>Bpm</label>
                <div className="centralizer">
                    <input hidden={entrance} value={bpm} type="number" min="1" onKeyDown={event=>{if((event.key != "Backspace" && (event.key.charCodeAt(0) < 48 || event.key.charCodeAt(0) > 57))) {event.preventDefault()}}}
                     onChange={e=>changeBPM(e.target.value)} onMouseUp={restart} onKeyUp={restart} className="form-control"/> 
                </div>
                </div> 
                <div className="controlBox noMargin">
                        <label  hidden={entrance} className="form-check-label mb-3" htmlFor="mainToggle">Start</label>
                        <div className="centralizer">
                        <div className="form-check form-switch leftMargin">
                            <input hidden={entrance} id="mainToggle" type="checkbox"checked={isRunning} onChange={changeRunState} onFocus={(e)=>e.target.blur()} className="form-check-input"  role="switch"/>
                            </div>
                        </div>
                </div>   
                </div>
                </div>
                   {/*  <CustomSelect options="Claves Hihat Kick Rim Snare" defaultValue="1"/> */}
                    <Metro index="1" name="metro1" start="4" globalTempo={bpm} metroEnable={isRunning} changeSubdivision={reset} note={49} restart={restart} entrance={entrance} gainStart={0.5} panningStart={0.4} soundStart={3} mouse={mouseDown} fineSelect={fineSelect}/>
                    <Metro index="2"  name="metro2" start="3" globalTempo={bpm} metroEnable={isRunning} changeSubdivision={reset} note={52} restart={restart} entrance={entrance} gainStart={0.5} panningStart={0.6} soundStart={1} mouse={mouseDown} fineSelect={fineSelect}/>
                </main>

                
            </div>
            )
}

