import React from "react"
import Cell from "../components/Cell"
import CustomSelect from "../components/CustomSelect"




export default function Metro(props){

    
    const [division, setDivision]=React.useState(props.start)
    const [cells, setCells]=React.useState(makeStartCells())
    const [count,setCount]=React.useState(1)
    const [panningMarkerLeft, setPanningMarkerLeft]=React.useState({"color":"white"})
    const [panningMarkerRight, setPanningMarkerRight]=React.useState({"color":"white"})
    const [gainMarkerLeft, setGainMarkerLeft]=React.useState({"color":"white"})
    const [gainMarkerRight, setGainMarkerRight]=React.useState({"color":"white"})
    const [forcer, setForcer]=React.useState(false)
    let cellsRef=React.useRef(cells)  //avoid infinite loop, caused by dependencyArray of useEffect...
    const masterTempo=bpmCalc(props.globalTempo, 1)
    const internTempo=bpmCalc(props.globalTempo, division)
    let gainOld=React.useRef(props.gainStart)
    let panningOld=React.useRef(props.panningStart)
    const [deaf, setDeaf]=React.useState(false)

    function changeDivision(newDivision){
        if(newDivision<1){setDivision(1)}
        else {setDivision(newDivision)
        props.changeSubdivision()
    }}
    function makeStartCells(){
        let startCells={}
        for(let i=0; i<props.start;i++){
        startCells={...startCells, [props.name+"-"+parseInt(i+1)]:false}
        }
        return startCells
    }
    function changeCells(cell){
        
        setCells((prevState)=>{return {...prevState, [cell]:!prevState[cell]}})}
        cellsRef.current=cells
        const stepElements=[]
        for(let i=0; i<division;i++){
        stepElements.push(<Cell entrance={props.entrance} midi={props.note} row={props.name} name={props.name+"-"+parseInt(i+1)} key={props.name+"-"+parseInt(i+1)} handleClick={changeCells} cellState={cells[props.name+"-"+parseInt(i+1)]} counter={count} on={props.metroEnable} mouse={props.mouse} division={division}/>)   
        
    }


    
    function sync(){
        let syncCells={}
        for(let i=0;i<division;i++){
        syncCells={...syncCells, [props.name+"-"+parseInt(i+1)]:cellsRef.current[props.name+"-"+parseInt(i+1)]===undefined ? false: cellsRef.current[props.name+"-"+parseInt(i+1)]}
        }
       setCells(syncCells)
    }

    function masterClock(){
        if(props.metroEnable){
        /* console.log("test") */
        const interval=setInterval(()=>{setCount(1); setForcer(prevState=>prevState+1); internClock()}, masterTempo)
        return () => clearInterval(interval)}
        else{console.log("stop!")}
    }

    function internClock(){
        let delay
        const Timer=()=>setTimeout(()=>{setCount(prevState=>{return prevState+1}); console.log(count)},delay)
        {
            for(let i=1;i<division;i++){
                if(props.metroEnable){delay=internTempo*i;Timer()}
                if(!props.metroEnable){var id = window.setTimeout(function() {}, 0);
                while (id--) {
                    window.clearTimeout(id); // will do nothing if no timeout with id is present
                }
            }    
    }}}

    function bpmCalc(bpm, division){
        return 60000/bpm/division*4
    }
    
    function startCounting(){
       if(props.metroEnable){setCount(1);setForcer(prevState=>!prevState); console.log(count)}
       }
    

    function countCellValues(index){
        const normalIndex=parseInt(index)-1
        const cellsArray=Object.values(cells)
        console.log(cellsArray[normalIndex])
    }

function changeSound(value){
    document.getElementById("sound"+props.index).value=value;
    document.getElementById("sound"+props.index).dispatchEvent(new InputEvent("input"));
    }
    

function changeGain(value){
    document.getElementById("gain"+props.index).value=value;
    document.getElementById("gain"+props.index).dispatchEvent(new InputEvent("input"));
    if(value==0){setDeaf(true)}
    else{setDeaf(false)}
}


function changePanning(value){
    document.getElementById("panning"+props.index).value=value;
    document.getElementById("panning"+props.index).dispatchEvent(new InputEvent("input"));
}

function noPanningMarker(){
    setPanningMarkerLeft({"color":"white"})
    setPanningMarkerRight({"color":"white"})
}

function panningMarker(value){
    const tempArray=[panningOld.current]
    tempArray.push(value)
    console.log(tempArray)
    if(tempArray[1]<tempArray[0]){setPanningMarkerLeft({"fontSize":"20px"});setPanningMarkerRight({"color":"white"})}
    else {setPanningMarkerRight({"fontSize":"20px"});setPanningMarkerLeft({"color":"white"})}
    tempArray.shift()
    panningOld.current=tempArray[0]
}

function noGainMarker(){
    setGainMarkerLeft({"color":"white"})
    setGainMarkerRight({"color":"white"})
}

function gainMarker(value){
    
    const tempArray=[gainOld.current]
    tempArray.push(value)
    console.log(tempArray)
    if(tempArray[1]<tempArray[0]){setGainMarkerLeft({"fontSize":"20px"});setGainMarkerRight({"color":"white"})}
    else {setGainMarkerRight({"fontSize":"20px"});setGainMarkerLeft({"color":"white"})}
    tempArray.shift()
    gainOld.current=tempArray[0]
}

function clearCells(){
    let startCells={}
    for(let i=0; i<division;i++){
    startCells={...startCells, [props.name+"-"+parseInt(i+1)]:false}
    }
    setCells(startCells)
}





       
        React.useEffect(()=>console.log(cells), [cells])
        React.useEffect(sync,[division,props.name,cellsRef])
        React.useEffect(startCounting, [props.metroEnable])
        React.useEffect(masterClock, [props.metroEnable])
        React.useEffect(internClock, [props.metroEnable])
        React.useEffect(()=>console.log(props.metroEnable), [props.metroEnable])
        React.useEffect(()=>{if(division==1){setCount(2)}},[forcer])

      

        /* React.useEffect(()=>props.fineSelect, [selection]) */


   

    return(
        
        <div hidden={props.entrance} className="metroSuperContainer mt-5 p-4">
        <div className="metroWrapper mb-4">
            <div className="controlBox verticalizer test">
                <label className="mb-3" hidden={props.entrance}>Grid</label>
                <div className="centralizer">
                    <input hidden={props.entrance}  name={props.name} value={division} type="number" onChange={e=>{changeDivision(e.target.value)}} onMouseUp={props.restart} onKeyUp={props.restart} className="form-control"/>
                </div>
            </div>
            <div className="controlBox">
                <label className="mb-3" hidden={props.entrance}>Sound</label>
                <div className="centralizer">
                    <CustomSelect options="Claves Hihat Kick Rim Snare" hidden={props.entrance} defaultValue={props.soundStart} onInput={(value)=>changeSound(value)} /> 
                </div>
            </div>
            <div className="controlBox">
                <label className="mb-3" hidden={props.entrance}>Gain</label>
                <div className="panel">
                <div className="centralizer">
                    <label className="fixerLeft ms-2" style={gainMarkerLeft}><i className={deaf?"fa-solid fa-volume-xmark":"fa-solid fa-volume-low"}></i></label>
                    <input hidden={props.entrance} defaultValue={props.gainStart} className="controlSlider ms-3 me-2" type="range" onInput={(e)=>{changeGain(e.target.value);gainMarker(e.target.value)}} onMouseUp={noGainMarker} min="0" max="1" step="0.01"/>
                    <label className="fixerRight" style={gainMarkerRight}><i className="fa-solid fa-volume-high"></i></label>
                </div>
                </div>
            </div>
            <div >
                <label className="mb-3" hidden={props.entrance}>Panning</label>
                <div className="panel">
                <div className="centralizer">
                    
               
                    <label className="fixerLeft ms-2" style={panningMarkerLeft}><i className="fa-solid fa-ear-listen fa-flip-horizontal"></i></label>
                    <input hidden={props.entrance} defaultValue={props.panningStart} className="controlSlider ms-3 me-2" type="range" onInput={(e)=>{changePanning(e.target.value);panningMarker(e.target.value)}} onMouseUp={noPanningMarker} min="0" max="1" step="0.01"/>
                    <label className="fixerRight" style={panningMarkerRight}><i className="fa-solid fa-ear-listen"></i></label>
               
                    
                    
                </div>
                </div>
            </div>

            <div className="toRight">
            <label className="mb-3 ms-auto textRight" hidden={props.entrance}>Clear</label>
                
                <div className="centralizer">

                    <button className="btn btn-light ms-5 smallClear" onClick={clearCells}><i className="fa-regular fa-trash-can"style={{color: "grey"}}></i></button>

                    
                </div>
            </div>

        </div>
                
                <div className="cellContainer">{stepElements}</div>
        
          <div className="controlWrapper mt-3">
      

      </div>
      </div>
   
    )
}