import React from "react"
import { OverlayTrigger, Tooltip } from 'react-bootstrap'

export default function Cell(props){
  

   /* const active=props.cellState[props.name] */
   const active=props.cellState
   const marker= props.name===props.row+"-"+props.counter
   const activeClone=React.useRef(active)
   activeClone.current=active
   let styles={
    backgroundColor: "white" 
   }

   if(active&&props.division===1){styles={
    backgroundColor:props.on?"#FF0066":"#fe79ae" 
   }}
   else{

   if(active&&marker){styles={
    backgroundColor: props.on?"#FF0066":"#fe79ae" 
   }}

   else if(active&&!marker){styles={
    backgroundColor: "#fe79ae" 
   }}

   else if(!active&&!marker){styles={
    backgroundColor: "white" 
   }}

   else{styles={
    backgroundColor: props.on?"#ddd":"white" 
   }}}



React.useEffect(()=>{console.log("count: "+props.counter);if(activeClone.current&&marker&&props.on){document.getElementById("id"+props.midi).click()}},[marker,props.midi, props.on])
/* React.useEffect(()=>{   if(!activeClone.current&&!props.on){document.getElementById("id"+props.midi).click()}},[props.on, props.midi])    */
    return (
        <div hidden={props.entrance} style={styles} name={props.name} className="cell" onMouseOver={()=>{if(props.mouse.current){props.handleClick(props.name) ;if(!props.on&&!activeClone.current){document.getElementById("id"+props.midi).click()}}}} onMouseDown={e=>{props.handleClick(props.name); if(!props.on&&!activeClone.current){document.getElementById("id"+props.midi).click()} }} onPointerDown={()=>{console.log(props.on,activeClone.current);}}>
        <OverlayTrigger
        placement={'bottom'}
        delay={{ show: 1000, hide: 100 }}
        overlay={
        <Tooltip>
        {props.name.slice(7)} / {props.division} 
        </Tooltip>}>
       <div className="filler"></div>
        </OverlayTrigger>
        </div>
    )
}