import React from "react"


export default function CustomSelect(props){


    
const [isShowingDropdown, setIsShowingDropdown]=React.useState(false)
const optionsArray=props.options.split(" ")
const selectedValue=React.useRef(props.defaultValue) 
function selectItem(number){
    const display=optionsArray[number]
    return display}

const [selection, setSelection]=React.useState(selectItem(props.defaultValue))
const optionsEl=optionsArray.map((option, index)=>{return <div key={"optionsWrapper"+index} style={{position:"relative"}}><p className={selectedValue.current===index ? "currentSelection customOption":"customOption"} style={{position:"absolute", top:index*2+"em", paddingLeft:"0.5em", width:"7.5em", border:"0px solid grey", cursor:"pointer", opacity:"90%", color:"grey", backgroundColor:"white", boxShadow:"inset 1px 1px 4px #1b3855", borderRadius:"4px", fontSize:"0.8em", height:"2em", paddingTop:"0.2em"}} key={index} onClick={()=>{setSelection(selectItem(index));setIsShowingDropdown(false);props.onInput(index);selectedValue.current=index}} hidden={!isShowingDropdown} >{option}</p> </div>})

console.log(selectedValue)

    return(
        <div onMouseLeave={()=>{if(isShowingDropdown){setIsShowingDropdown(false)}}}>
        <button className="mb-1 customSelectButton" style={{width:"6em", borderRadius:"5px", backgroundColor:"white", border:"1px solid whitesmoke", textAlign:"left"}} onClick={()=>setIsShowingDropdown(prevState=>!prevState)}> <div style={{color:"grey"}} className="d-flex justify-content-between align-items-center fontControl">{selection}<i className="fa-solid fa-caret-down" style={{color:"grey"}}></i></div></button>
        <div>{optionsEl}</div>
        </div>
        
    )


}